/**
 * 
 * view_materialclick_material$AppViewScreen$pageview, $AppStart
 * 'view_material'=========
 * 'click_material'========
 * '$AppViewScreen'========h5$referrerurl
 * '$pageview'=============h5$AppViewScreen
 * '$AppStart'=============
 */
import VueCookies from "vue-cookies";
// import MobileDetect from '../../public/js/mobile-detect.min.js'
import axios from 'axios';
import Cookies from 'js-cookie'

export default {
    /**
     *
     */
    register() {
        return {
            time: new Date().getTime(),
            lib: '',
            // 
            $os: this.detectOS()[1],
            // 
            $os_version: this.detectOS()[1],
            // 
            $carrier: '',
            // 
            $manufacturer: this.detectOS()[2],
            // 
            $model: this.detectOS()[0],
            // 
            $network_type: this.getNetworkType()[0],
            // app
            $app_version: '',
            // WIF
            $wifi: this.getNetworkType()[0] == 'wifi' ? true : false,
            // IP
            ip: sessionStorage.getItem('ip')?sessionStorage.getItem('ip'):'',
            // 
            $lib_version: '',
            // IDuser_iduser_idUVdistinct user_id
            uid: '',
            // (nativecontrollerurl scheme,H5Weblink)
            $url: '',
            // URL
            $referrer: '',
            // app
            source: '',
            // Android
            utdid: '',
            // Android
            imei: '',
            // iOS
            idfa: '',
            // iOS
            idfv: '',
            // AB
            ab_group: '',
        };
    },

    pageview(parms) {
        return {
            $url: location.href,
            $referrer: Cookies.get("referrer")?Cookies.get("referrer"):'',
            page_type: parms.page_type?parms.page_type:'',
            referrer_page_type: parms.referrer_page_type?parms.referrer_page_type:'',
            page_attr: parms.page_attr,
            item_id: parms.item_id?parms.item_id:Cookies.get("storeOrganId"),
            item_type: parms.item_type?parms.item_type:"store_id",
            sell_label: "",
            params:parms.params?parms.params:'',
            is_return:null,
            session:parms.session?parms.session:''
        }
    },

    material(parms) {
        return {
            platform:this.detectOS()[1],
            // 
            page_type: parms.page_type,
            // 
            current_page_url: location.href,
            // id
            page_attr: parms.attr,
            // id
            item_id: Cookies.get("storeOrganId") ? Cookies.get("storeOrganId") : '',
            // 
            item_type: "store_id",
            // 
            card_type: parms.card_type,
            // 
            card_postion: '0',
            // ID
            card_id: '',
            // materialid
            material_id: '',
            // material
            material_type: parms.material_type,
            // 
            material_link: parms.material_link ? parms.material_link : '',
            // 
            material_name: '',
            // 
            material_attr: '',
            // id
            material_item_id: '',
            // 
            material_item_type: ''
        }

    },


    /**
     * 
     * @param type 
     * @param parms 
     */
    send(type, parms) {
        //  
        window.sensors.registerPage(this.register());
        // 
        window.sensors.track(type, this.material(parms));
    },

    /**
     *  sensors.login 
     * @param memberId
     */
    sendLogin(memberId) {
        window.sensors.registerPage(this.register());
        window.sensors.login(memberId);
        return [memberId]
    },

    /**
     *  sensors.logout  id
     */
    logout() {
        window.sensors.logout();
    },
    /**
     * 
     * @param type 
     * @param parms 
     */
    view(type, parms) {
        window.sensors.registerPage(this.register());
        // 
        window.sensors.track(type, this.pageview(parms));
    },

    /**
     * 
     * @param element elementid
     * @param type 
     * @param parms 
     */
    load(element, type, parms) {
        window.sensors.registerPage(this.register());
        var settime;
        var el = document.getElementById(element);
        var io = new IntersectionObserver(
            entries => {
                // 
                clearTimeout(settime);
                // 1 1
                if (entries[0].isIntersecting) {
                    settime = setTimeout(() => {
                        this.send(type, this.material(parms));
                    }, 1000)
                }
            }, {});
        io.observe(el);
    },

    /**
     * 
     */
    detectOS() {
        //
        Array.prototype.contains = function (needle) {
            for (i in this) {
                if (this[i].indexOf(needle) > 0)
                    return i;
            }
            return -1;
        };
        var device_type = navigator.userAgent;//userAgent
        var md = new MobileDetect(device_type);//mobile-detect
        var os = md.os();//
        var mf = md.mobile()
        var model = "";
        if (os === "iOS") {//ios
            os = md.os() + md.version("iPhone");
            model = md.mobile();
        } else if (os === "AndroidOS") {//Android
            os = md.os() + md.version("Android");
            var sss = device_type.split(";");
            var i = sss.contains("Build/");
            if (i > -1) {
                model = sss[i].substring(0, sss[i].indexOf("Build/"));
            }
        }
        return [model, os, mf]
    },
    /**
     * 
     */
    getNetworkType() {
        var ua = navigator.userAgent;
        var networkStr = ua.match(/NetType\/\w+/) ? ua.match(/NetType\/\w+/)[0] : 'NetType/other';
        networkStr = networkStr.toLowerCase().replace('nettype/', '');
        var networkType;
        switch (networkStr) {
            case 'wifi':
                networkType = 'wifi';
                break;
            case '4g':
                networkType = '4g';
                break;
            case '3g':
                networkType = '3g';
                break;
            case '3gnet':
                networkType = '3g';
                break;
            case '2g':
                networkType = '2g';
                break;
            default:
                networkType = 'other';
        }
        return [networkStr]
    }
}

