import popRefuse from './popRefuse.vue'

const Toast = {};
// Toast
//instance popRefusepopRefusethis
//config  vue
Toast.install = function (Vue) {
    // Vue
    // 
    const ToastConstructor = Vue.extend(popRefuse);
    // 
    const instance = new ToastConstructor();
    // div
    // div
    instance.$mount(document.createElement('div'));
    document.body.appendChild(instance.$el);
    // Vue
    // 
    Vue.prototype.$popRefuse = (config) => {
        console.log(config instanceof String);
        if (typeof (config) === 'string' || typeof (config) === 'number') {
            instance.message = config;
            instance.visible = true;
            instance.okClick = () => {
                instance.visible = false
            };
            instance.cancelClick = () => {
                instance.visible = false
            };
            return
        }
        console.log('231',config);
        instance.message = config.content;
        instance.visible = true;
        //
        instance.confirm = config.onOK ? function () {
            instance.visible = false;//
            config.onOK(instance.refuse) //
        } : function () {
            instance.visible = false
        };
        instance.cancel = config.onCancel ? function (e) {
            instance.visible = false;
            config.onCancel(instance.refuse)
        } : function () {
            instance.visible = false
        }
    }
};

export default Toast
